import React, { useEffect, useState } from 'react';
import './App.css';
import TicketBox from './components/TicketBox';
import TimeTable from './components/TimeTable';
import { APP_CONFIG } from './data/config';
import moment from 'moment';
import Swal from 'sweetalert2';
import { IconButton, Dialog, DialogTitle, DialogContent, DialogActions, Button } from '@mui/material';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import CheckIcon from '@mui/icons-material/Check';
import { ReactComponent as FacebookIcon } from './assets/facebook.svg';
import { ReactComponent as LineIcon } from './assets/line.svg';

const STATIONS = ["", "", "", "", "", "", "", "", "", "", "", ""];

//  latebird query ?latebird||||||
const parseLatebirdQuery = (search) => {
    if (!search) return null;
    
    //  URL  %7C 
    const decodedSearch = decodeURIComponent(search);
    
    if (!decodedSearch.startsWith('?latebird|')) return null;
    
    const parts = decodedSearch.substring(1).split('|'); //  ? 
    if (parts.length !== 7) return null;
    
    const [, orderHash, dateStr, trainNumber, fromStationIdx, toStationIdx, ticketCount] = parts;
    
    const fromIdx = parseInt(fromStationIdx, 10);
    const toIdx = parseInt(toStationIdx, 10);
    
    if (isNaN(fromIdx) || isNaN(toIdx) || fromIdx < 0 || fromIdx > 11 || toIdx < 0 || toIdx > 11) {
        return null;
    }
    
    return {
        queryType: 'latebird',
        orderHash: `#${orderHash}`,
        selectedDate: moment(dateStr, 'YYYY-MM-DD'),
        target: trainNumber,
        fromStation: STATIONS[fromIdx],
        toStation: STATIONS[toIdx],
        ticketCount: parseInt(ticketCount, 10),
        isLatebirdOrder: true //  query 
    };
};

//  ticket query ?ticket||()||
const parseTicketQuery = (search) => {
    if (!search) return null;
    
    //  URL  %7C 
    const decodedSearch = decodeURIComponent(search);
    
    if (!decodedSearch.startsWith('?ticket|')) return null;
    
    const parts = decodedSearch.substring(1).split('|'); //  ? 
    if (parts.length !== 5) return null;
    
    const [, dateStr, trainNumbers, fromStationIdx, toStationIdx] = parts;
    
    const fromIdx = parseInt(fromStationIdx, 10);
    const toIdx = parseInt(toStationIdx, 10);
    
    if (isNaN(fromIdx) || isNaN(toIdx) || fromIdx < 0 || fromIdx > 11 || toIdx < 0 || toIdx > 11) {
        return null;
    }
    
    // 3
    const trainList = trainNumbers.split(',').map(t => t.trim()).filter(t => t);
    const target = trainList[0] || null;
    const alternative = trainList.slice(1, 4); // 3
    
    return {
        queryType: 'ticket',
        selectedDate: moment(dateStr, 'YYYY-MM-DD'),
        target: target,
        alternative: alternative,
        fromStation: STATIONS[fromIdx],
        toStation: STATIONS[toIdx],
        isLatebirdOrder: false // 
    };
};

//  share query ?share||||
const parseShareQuery = (search) => {
    if (!search) return null;
    
    //  URL  %7C 
    const decodedSearch = decodeURIComponent(search);
    
    if (!decodedSearch.startsWith('?share|')) return null;
    
    const parts = decodedSearch.substring(1).split('|'); //  ? 
    if (parts.length !== 5) return null;
    
    const [, dateStr, trainNumber, fromStationIdx, toStationIdx] = parts;
    
    const fromIdx = parseInt(fromStationIdx, 10);
    const toIdx = parseInt(toStationIdx, 10);
    
    if (isNaN(fromIdx) || isNaN(toIdx) || fromIdx < 0 || fromIdx > 11 || toIdx < 0 || toIdx > 11) {
        return null;
    }
    
    return {
        queryType: 'share',
        selectedDate: moment(dateStr, 'YYYY-MM-DD'),
        target: trainNumber,
        alternative: [],
        fromStation: STATIONS[fromIdx],
        toStation: STATIONS[toIdx],
        isLatebirdOrder: false,
        isShareMode: true // 
    };
};

//  useEffect 
const initialQueryData = parseLatebirdQuery(window.location.search) || parseTicketQuery(window.location.search) || parseShareQuery(window.location.search);

function App() {
    const [selectedTrain, setSelectedTrain] = useState(initialQueryData || {});
    const [showTicketBox, setShowTicketBox] = useState(!!initialQueryData);
    const [isLoadingQuery, setIsLoadingQuery] = useState(!!initialQueryData);
    const [showHelpModal, setShowHelpModal] = useState(false);

    // 
    const showQueryError = (message) => {
        Swal.fire({
            icon: 'error',
            title: '',
            text: message,
            confirmButtonColor: '#ff6b00'
        }).then(() => {
            setShowTicketBox(false);
            setSelectedTrain({});
        });
    };

    useEffect(() => {
        // 
        document.title = APP_CONFIG.title;
        
        //  query string API 
        if (initialQueryData) {
            //  | 
            let queryString = decodeURIComponent(window.location.search.substring(1)); //  ? 
            //  share  ticket  API
            if (queryString.startsWith('share|')) {
                queryString = 'ticket' + queryString.substring(5);
            }
            const apiUrl = `https://grassboy.tw/tmp/thsrc.api.php?op=get_latebird&q=${encodeURIComponent(queryString)}`;
            fetch(apiUrl)
                .then(response => response.json())
                .then(data => {
                    if (data.ok) {
                        //  selectedTrain 
                        //  is_confirm 
                        // - is_confirm: true price_orig
                        // - is_confirm: false price_min
                        const isConfirm = data.train.is_confirm;
                        setSelectedTrain(prev => ({
                            ...prev,
                            departureTime: data.train.start,
                            arrivalTime: data.train.end,
                            duration: data.train.duration,
                            isConfirm: isConfirm,
                            price: isConfirm ? '' : data.train.discount, // 
                            priceColor: isConfirm ? null : data.train.price_color, // 
                            unitPrice: isConfirm ? data.train.price_orig : data.train.price_min, // 
                            //  URL 
                            alternative: isConfirm ? (prev.alternative || []) : []
                        }));
                    } else {
                        // API 
                        showQueryError('');
                    }
                })
                .catch(error => {
                    console.error('Failed to fetch train info:', error);
                    showQueryError('');
                })
                .finally(() => {
                    setIsLoadingQuery(false);
                });
        }
    }, []);

    return (
        <div className="App">
            {/*  - share  */}
            {!selectedTrain.isShareMode && (
            <div className="landscape-overlay">
                <div className="landscape-message">
                    <div className="rotate-icon"></div>
                    <p></p>
                </div>
            </div>
            )}
            
            <div className="app-navbar">
                <div className="navbar-content">
                    <h1 className="app-title">{APP_CONFIG.title}</h1>
                    <IconButton 
                        className="help-button"
                        onClick={() => setShowHelpModal(true)}
                        sx={{ color: '#fff', padding: '4px' }}
                    >
                        <HelpOutlineIcon />
                    </IconButton>
                </div>
            </div>
            <div className="app-content">
                {/*  */}
                {isLoadingQuery && (
                    <div className="loading-container" style={{ 
                        display: 'flex', 
                        justifyContent: 'center', 
                        alignItems: 'center', 
                        minHeight: '200px',
                        color: '#666'
                    }}>
                        <p>...</p>
                    </div>
                )}
                {/*  */}
                {!isLoadingQuery && showTicketBox && selectedTrain && selectedTrain.target ? (
                    <TicketBox 
                        selectedTrain={selectedTrain} 
                        onBack={() => {
                            //  TimeTable 
                            //  selectedDate  departureTime 
                            let adjustedDate = selectedTrain.selectedDate ? selectedTrain.selectedDate.clone() : null;
                            if (adjustedDate && selectedTrain.departureTime) {
                                const [hours, minutes] = selectedTrain.departureTime.split(':');
                                adjustedDate = adjustedDate.hour(parseInt(hours)).minute(parseInt(minutes)).subtract(30, 'minutes');
                            }
                            setSelectedTrain({
                                ...selectedTrain,
                                selectedDate: adjustedDate,
                                departureTime: adjustedDate ? adjustedDate.format('HH:mm') : null,
                                target: null,
                                alternative: [],
                                isLatebirdOrder: false, // 
                                isConfirm: undefined //  TimeTable 
                            });
                            setShowTicketBox(false);
                            //  URL  query string
                            window.history.replaceState({}, '', window.location.pathname);
                        }}
                        onReturnTrip={() => {
                            // 
                            setSelectedTrain({
                                ...selectedTrain,
                                fromStation: selectedTrain.toStation,
                                toStation: selectedTrain.fromStation,
                                target: null,
                                alternative: []
                            });
                            setShowTicketBox(false);
                        }}
                    />
                ) : !isLoadingQuery && (
                    <TimeTable
                        initialDate={selectedTrain.selectedDate}
                        initialTime={selectedTrain.departureTime}
                        initialFromStation={selectedTrain.fromStation}
                        initialToStation={selectedTrain.toStation}
                        onTrainSelect={(train_select)=>{
                            setSelectedTrain({
                                ...selectedTrain,
                                ...train_select
                            });
                        }}
                        onNextStep={() => {
                            setShowTicketBox(true);
                        }}
                    />
                )}
            </div>

            {/*  Modal */}
            <Dialog 
                open={showHelpModal} 
                onClose={() => setShowHelpModal(false)}
                maxWidth="sm"
                fullWidth
            >
                <DialogTitle sx={{ fontWeight: 'bold', color: '#333' }}>
                    
                </DialogTitle>
                <DialogContent dividers>
                    <p style={{ marginBottom: '16px', lineHeight: 1.8 }}>
                        <a href="https://www.facebook.com/groups/258639338299876" target="_blank" rel="noopener noreferrer" style={{ color: '#1877f2' }}></a>
                    </p>
                    
                    <h4 style={{ color: '#000', fontWeight: 'bold', marginBottom: '8px' }}></h4>
                    <p style={{ marginBottom: '16px', lineHeight: 1.8 }}>
                        <em style={{ color: '#ff6b00', fontStyle: 'normal', fontWeight: 'bold' }}></em><em style={{ color: '#ff6b00', fontStyle: 'normal', fontWeight: 'bold' }}>200</em>
                    </p>
                    
                    <h4 style={{ color: '#000', fontWeight: 'bold', marginBottom: '8px' }}></h4>
                    <p style={{ marginBottom: '16px', lineHeight: 1.8 }}>
                        <span style={{ color: '#999', fontSize: '0.9em' }}>()</span>
                    </p>
                    
                    <p style={{ marginBottom: '16px', lineHeight: 1.8 }}>
                         <a href="https://lin.ee/mo8POhG" target="_blank" rel="noopener noreferrer" style={{ color: '#06c755' }}>Line</a> <br />
                        Line or  Line <span style={{ color: '#999', fontSize: '0.9em' }}>()</span><br />
                         Line  Line ~
                    </p>
                    
                    <p style={{ marginBottom: '0', lineHeight: 1.8 }}>
                        ~
                    </p>
                </DialogContent>
                <DialogActions sx={{ padding: '16px', gap: '8px', justifyContent: 'center' }}>
                    <IconButton 
                        href="https://www.facebook.com/groups/258639338299876"
                        target="_blank"
                        sx={{ 
                            backgroundColor: '#1877f2', 
                            color: '#fff',
                            width: 48,
                            height: 48,
                            borderRadius: '5px',
                            '&:hover': { backgroundColor: '#166fe5' } 
                        }}
                    >
                        <FacebookIcon style={{ width: 28, height: 28 }} />
                    </IconButton>
                    <IconButton 
                        href="https://lin.ee/mo8POhG"
                        target="_blank"
                        sx={{ 
                            backgroundColor: '#06c755', 
                            color: '#fff',
                            width: 48,
                            height: 48,
                            borderRadius: '5px',
                            '&:hover': { backgroundColor: '#05b34c' } 
                        }}
                    >
                        <LineIcon style={{ width: 28, height: 28 }} />
                    </IconButton>
                    <Button 
                        variant="contained"
                        color="primary"
                        size="large"
                        onClick={() => setShowHelpModal(false)}
                        sx={{ 
                            width: '100%',
                            height: 48,
                            borderRadius: '5px',
                            fontSize: '1.2rem',
                            fontWeight: 'bold',
                            textIndent: '-0.5em',
                            '&:hover': { backgroundColor: '#999999' } 
                        }}
                    >
                        <CheckIcon style={{ width: 28, height: 28, transform: 'translateX(-0.5em)' }} /> 
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default App;