import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App'; // force rebuild
import { ThemeProvider, createTheme } from '@mui/material/styles';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';

const theme = createTheme({
    palette: {
        primary: {
            main: '#ca4f0f',  // 
            light: '#ff7f3f',
            dark: '#962100'
        },
        secondary: {
            main: '#1976d2',  // 
            light: '#42a5f5',
            dark: '#1565c0'
        },
        error: {
            main: '#d32f2f',  // 
            light: '#ef5350',
            dark: '#c62828'
        },
        warning: {
            main: '#ed6c02',  // 
            light: '#ff9800',
            dark: '#e65100'
        },
        info: {
            main: '#0288d1',  // 
            light: '#03a9f4',
            dark: '#01579b'
        },
        success: {
            main: '#2e7d32',  // 
            light: '#4caf50',
            dark: '#1b5e20'
        }
    },
});

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <React.StrictMode>
        <ThemeProvider theme={theme}>
            <App />
        </ThemeProvider>
    </React.StrictMode>
);

// Register service worker for PWA
serviceWorkerRegistration.register();
