import React, { useState } from 'react';
import './TimeTable.css';
import util from '../common/util';
import Swal from 'sweetalert2';

import SyncAltIcon from '@mui/icons-material/SyncAlt';
import SearchIcon from '@mui/icons-material/Search';

import { Button, FormControlLabel, Radio, RadioGroup, Checkbox, Select, FormControl, Chip } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import moment from 'moment';
import 'moment/locale/zh-tw';
import { AdapterMoment } from '@mui/x-date-pickers/AdapterMoment';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';
import { zhTW } from '@mui/x-date-pickers/locales';

//  moment 
moment.updateLocale('zh-tw', {
    weekdaysMin: ['', '', '', '', '', '', ''],
    weekdaysShort: ['', '', '', '', '', '', '']
});
moment.locale('zh-tw');

// id  API =0, =1, ... =11
const STATION_LIST = [
    { id: 0, name: '' },
    { id: 1, name: '' },
    { id: 2, name: '' },
    { id: 3, name: '' },
    { id: 4, name: '' },
    { id: 5, name: '' },
    { id: 6, name: '' },
    { id: 7, name: '' },
    { id: 8, name: '' },
    { id: 9, name: '' },
    { id: 10, name: '' },
    { id: 11, name: '' },
];

const API_BASE_URL = 'https://grassboy.tw/tmp/thsrc.api.php';

// localStorage key
const STORAGE_KEY = 'thsrc_timetable';

//  localStorage 
const getInitialDateTime = () => {
    try {
        const saved = localStorage.getItem(STORAGE_KEY);
        if (saved) {
            const data = JSON.parse(saved);
            if (data.dateTime) {
                const savedMoment = moment(data.dateTime, 'YYYY-MM-DD HH:mm');
                const now = moment();
                
                //  HH:MM 
                if (savedMoment.isBefore(now)) {
                    const hours = savedMoment.hours();
                    const minutes = savedMoment.minutes();
                    let newDate = moment().hour(hours).minute(minutes).second(0);
                    
                    // 
                    if (newDate.isBefore(now)) {
                        newDate.add(1, 'day');
                    }
                    return newDate;
                }
                return savedMoment;
            }
        }
    } catch (e) {
        console.error('Error reading localStorage:', e);
    }
    return moment().subtract(20, 'minutes');
};

//  localStorage 
const getInitialStations = () => {
    try {
        const saved = localStorage.getItem(STORAGE_KEY);
        if (saved) {
            const data = JSON.parse(saved);
            return {
                from: data.fromStation || '',
                to: data.toStation || ''
            };
        }
    } catch (e) {
        console.error('Error reading localStorage:', e);
    }
    return { from: '', to: '' };
};

const TimeTable = ({ onTrainSelect, onNextStep, initialDate, initialTime, initialFromStation, initialToStation }) => {
    const initialStations = getInitialStations();
    const [selectedTrain, setSelectedTrain] = useState('');
    const [alternativeTrains, setAlternativeTrains] = useState([]);
    const [fromStation, setFromStation] = useState(initialFromStation || initialStations.from);
    const [toStation, setToStation] = useState(initialToStation || initialStations.to);
    const [selectedDate, setSelectedDate] = useState(initialDate || getInitialDateTime());
    const [isWideScreen, setIsWideScreen] = useState(window.innerWidth > 500);
    const [schedules, setSchedules] = useState([]);
    const [prevTime, setPrevTime] = useState(null);
    const [nextTime, setNextTime] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [hasSearched, setHasSearched] = useState(false); // 
    const [isConfirm, setIsConfirm] = useState(true); // false 
    const [errorMessage, setErrorMessage] = useState(null); // API 

    //  ID
    const getStationId = (stationName) => {
        const station = STATION_LIST.find(s => s.name === stationName);
        return station ? station.id : null;
    };

    //  URL  ticket 
    // trainNumbers: 
    const updateUrlToTicket = (date, trainNumber, from, to, alternatives = []) => {
        const dateStr = date.format('YYYY-MM-DD');
        const fromId = getStationId(from);
        const toId = getStationId(to);
        //  + 3
        const allTrains = [trainNumber, ...alternatives.slice(0, 3)].join(',');
        const ticketUrl = `?ticket|${dateStr}|${allTrains}|${fromId}|${toId}`;
        window.history.pushState({}, '', ticketUrl);
    };

    //  API 
    // clearSelection: /
    // direction: 'next' , 'prev' , null 
    const fetchTrains = async (from, to, date, time, clearSelection = true, direction = null) => {
        setIsLoading(true);
        // 
        setSchedules([]);
        try {
            const fromId = getStationId(from);
            const toId = getStationId(to);
            const timeStr = time || date.format('HH:mm');
            
            // 
            let actualDate = date.clone();
            
            //  time  selectedDate 
            if (time) {
                const [hours, minutes] = time.split(':');
                const originalTime = date.format('HH:mm');
                actualDate = actualDate.hour(parseInt(hours)).minute(parseInt(minutes));
                
                // 
                if (direction === 'next' && time < originalTime) {
                    // 
                    actualDate = actualDate.add(1, 'day');
                } else if (direction === 'prev' && time > originalTime) {
                    // 
                    actualDate = actualDate.subtract(1, 'day');
                }
                setSelectedDate(actualDate);
            }
            
            const dateStr = actualDate.format('YYYY-MM-DD');
            const url = `${API_BASE_URL}?from=${fromId}&to=${toId}&date=${dateStr}&time=${timeStr}`;
            const response = await fetch(url);
            const data = await response.json();
            var addi_text_map = {
                " 2026-01-14 ~": [new Date('2026-02-13').getTime(), new Date('2026-02-23').getTime()],
                " 2026-01-27 ~": [new Date('2026-02-26').getTime(), new Date('2026-03-02').getTime()],
                " 2026-03-03 ~": [new Date('2026-04-02').getTime(), new Date('2026-04-07').getTime()],
                " 2026-03-31 ~": [new Date('2026-04-30').getTime(), new Date('2026-05-04').getTime()],
                " 2026-04-08 ~": [new Date('2026-05-08').getTime(), new Date('2026-05-11').getTime()],
                " 2026-05-19 ~": [new Date('2026-06-18').getTime(), new Date('2026-06-22').getTime()],
                " 2026-08-25 ~": [new Date('2026-09-24').getTime(), new Date('2026-09-29').getTime()],
                " 2026-09-08 ~": [new Date('2026-10-08').getTime(), new Date('2026-10-12').getTime()],
                " 2026-09-23 ~": [new Date('2026-10-23').getTime(), new Date('2026-10-27').getTime()],
                " 2026-11-24 ~": [new Date('2026-12-24').getTime(), new Date('2026-12-28').getTime()],
                " 2026-12-01 ~": [new Date('2026-12-31').getTime(), new Date('2027-01-04').getTime()],
                " 30 ~": [new Date('2020-01-06').getTime(), new Date('2030-12-31').getTime()],
            };
                        
            // 
            if (data.error) {
                let finalErrorMessage = data.error;
                
                // 
                if (data.error === '') {
                    const queryTimestamp = actualDate.valueOf(); //  timestamp
                    
                    //  addi_text_map 
                    for (const [addiText, [startTime, endTime]] of Object.entries(addi_text_map)) {
                        if (queryTimestamp >= startTime && queryTimestamp <= endTime) {
                            finalErrorMessage = data.error + "\n" + addiText;
                            break; // 
                        }
                    }
                }
                
                setErrorMessage(finalErrorMessage);
                setSchedules([]);
                setHasSearched(true);
                return;
            }
            
            // 
            setErrorMessage(null);
            
            //  API 
            const trainList = data.trains.map(train => ({
                trainNumber: train.id,
                departureTime: train.start,
                arrivalTime: train.end,
                duration: train.duration,
                price: train.price,        // /9/8/65/5
                priceColor: train.price_color
            }));
            
            setSchedules(trainList);
            setPrevTime(data.prev_time);
            setNextTime(data.next_time);
            setHasSearched(true);
            setIsConfirm(data.is_confirm !== false); //  true false  false
            
            // 
            if (clearSelection) {
                setSelectedTrain('');
                setAlternativeTrains([]);
                
                // 
                onTrainSelect({
                    fromStation: from,
                    toStation: to,
                    selectedDate: date,
                    target: null,
                    alternative: []
                });
            }
        } catch (error) {
            console.error(':', error);
        } finally {
            setIsLoading(false);
        }
    };

    // 
    const handleSearch = () => {
        fetchTrains(fromStation, toStation, selectedDate, null, true);
    };

    // 
    const handlePrevTrains = () => {
        if (prevTime) {
            fetchTrains(fromStation, toStation, selectedDate, prevTime, false, 'prev');
        }
    };

    // 
    const handleNextTrains = () => {
        if (nextTime) {
            fetchTrains(fromStation, toStation, selectedDate, nextTime, false, 'next');
        }
    };
    
    //  localStorage
    const saveToLocalStorage = (from, to, date) => {
        try {
            const data = {
                fromStation: from,
                toStation: to,
                dateTime: date.format('YYYY-MM-DD HH:mm')
            };
            localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
        } catch (e) {
            console.error('Error saving to localStorage:', e);
        }
    };

    React.useEffect(() => {
        const handleResize = () => {
            setIsWideScreen(window.innerWidth > 500);
        };
        onTrainSelect({
            fromStation,
            toStation,
            selectedDate
        });
        window.addEventListener('resize', handleResize);
        
        //  initialTime 
        fetchTrains(fromStation, toStation, selectedDate, initialTime || null, !initialTime);
        
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    //  props  state
    React.useEffect(() => {
        if (initialFromStation) {
            setFromStation(initialFromStation);
        }
        if (initialToStation) {
            setToStation(initialToStation);
        }
    }, [initialFromStation, initialToStation]);

    //  localStorage
    React.useEffect(() => {
        saveToLocalStorage(fromStation, toStation, selectedDate);
    }, [fromStation, toStation, selectedDate]);

    // 
    const clearResults = () => {
        setSchedules([]);
        setSelectedTrain('');
        setAlternativeTrains([]);
        setPrevTime(null);
        setNextTime(null);
        setHasSearched(false);
        setErrorMessage(null);
    };

    const handleFromStationChange = (event) => {
        const newFromStation = event.target.value;
        // 
        if (newFromStation === toStation) {
            setToStation(fromStation);
        }
        setFromStation(newFromStation);
        clearResults();
    };

    const handleToStationChange = (event) => {
        const newToStation = event.target.value;
        // 
        if (newToStation === fromStation) {
            setFromStation(toStation);
        }
        setToStation(newToStation);
        clearResults();
    };

    const handleSwapStations = () => {
        const temp = fromStation;
        setFromStation(toStation);
        setToStation(temp);
        clearResults();
    };

    const handleDateChange = (newDate) => {
        setSelectedDate(newDate);
        clearResults();
    };

    const handleTrainChange = (event, schedule) => {
        if(selectedTrain === event.target.value){
            return;
        }
        // 
        setSelectedTrain(event.target.value);
        onTrainSelect({
            departureTime: schedule.departureTime,
            arrivalTime: schedule.arrivalTime,
            target: schedule.trainNumber,
            selectedDate: selectedDate, // 
            isConfirm: true // 
        });
    };

    const MAX_ALTERNATIVE_TRAINS = 3; // 

    const handleAlternativeChange = (event, trainNumber) => {
        event.stopPropagation();
        var new_value;
        if (event.target.checked) {
            // 
            if (alternativeTrains.length >= MAX_ALTERNATIVE_TRAINS) {
                Swal.fire({
                    text: '',
                    icon: 'warning',
                    confirmButtonColor: '#ff6b00'
                });
                return; // 
            }
            new_value = [...alternativeTrains, trainNumber];
        } else {
            new_value = alternativeTrains.filter(train => train !== trainNumber);
        }
        setAlternativeTrains(new_value);
        onTrainSelect({
            alternative: new_value
        });
    };

    // 
    const handleRemoveSelectedTrain = () => {
        // 
        if (alternativeTrains.length > 0) {
            const newSelectedTrain = alternativeTrains[0];
            const newAlternatives = alternativeTrains.slice(1);
            const schedule = schedules.find(s => s.trainNumber === newSelectedTrain);
            
            setSelectedTrain(newSelectedTrain);
            setAlternativeTrains(newAlternatives);
            onTrainSelect({
                departureTime: schedule?.departureTime,
                arrivalTime: schedule?.arrivalTime,
                target: newSelectedTrain,
                alternative: newAlternatives
            });
        } else {
            // 
            setSelectedTrain('');
            onTrainSelect({
                target: null,
                departureTime: null,
                arrivalTime: null
            });
        }
    };

    // 
    const handleRemoveAlternativeTrain = (trainNumber) => {
        const newAlternatives = alternativeTrains.filter(train => train !== trainNumber);
        setAlternativeTrains(newAlternatives);
        onTrainSelect({
            alternative: newAlternatives
        });
    };


    return (
        <div className="time-table">
            <div className="route-header">
                <div className="input-box d-flex flex-row flex-wrap justify-content-center">
                    <FormControl sx={{ minWidth: 90, flex:1 }}>
                        <Select
                            native
                            value={fromStation}
                            onChange={handleFromStationChange}
                            size="small"
                            sx={{
                                fontSize: '1.2rem',
                                fontWeight: 'bold',
                                '& .MuiNativeSelect-select': {
                                    padding: '6px 8px 5px 8px'
                                }
                            }}
                        >
                            {STATION_LIST.map((station) => (
                                <option key={station.id} value={station.name}>
                                    {station.name}
                                </option>
                            ))}
                        </Select>
                    </FormControl>
                    
                    <Button 
                        onClick={handleSwapStations}
                        variant="contained" 
                        sx={{
                            bgcolor: '#EEE', 
                            color: '#333', 
                            margin: '0 10px',
                            minWidth: '32px',
                            width: '32px',
                            height: '32px',
                            borderRadius: '50%',
                            padding: 0,
                            '&:hover': {
                                bgcolor: '#DDD'
                            }
                        }}
                    >
                        <SyncAltIcon sx={{ fontSize: '1.2rem' }} />
                    </Button>
                    
                    <FormControl sx={{ minWidth: 90, flex:1 }}>
                        <Select
                            native
                            value={toStation}
                            onChange={handleToStationChange}
                            size="small"
                            sx={{
                                fontSize: '1.2rem',
                                fontWeight: 'bold',
                                '& .MuiNativeSelect-select': {
                                    padding: '6px 8px 5px 8px'
                                }
                            }}
                        >
                            {STATION_LIST.map((station) => (
                                <option key={station.id} value={station.name}>
                                    {station.name}
                                </option>
                            ))}
                        </Select>
                    </FormControl>
                    <div className="date-info flex-row flex-nowrap justify-content-center">
                        <LocalizationProvider 
                            dateAdapter={AdapterMoment} 
                            adapterLocale="zh-tw"
                            localeText={zhTW.components.MuiLocalizationProvider.defaultProps.localeText}
                        >
                            <DateTimePicker
                                value={selectedDate}
                                onChange={handleDateChange}
                                className="flex-1"
                                format="MM/DD (ddd) HH:mm"
                                ampm={false}
                                sx={{
                                    maxWidth: '100%',
                                    '& .MuiInputBase-input': {
                                        fontSize: '1rem',
                                        color: '#666',
                                        padding: '8px',
                                        textAlign: 'center'
                                    }
                                }}
                            />
                        </LocalizationProvider>
                    </div>
                    <Button 
                        onClick={handleSearch}
                        variant="contained" 
                        color="primary"
                        disabled={isLoading}
                    >
                        <SearchIcon sx={{ fontSize: '1.2rem' }} />
                    </Button>
                </div>
            </div>
            
            <RadioGroup
                className="flex-nowrap"
                name="train-selection"
                value={selectedTrain}
                onChange={(event) => {
                    const schedule = schedules.find(s => s.trainNumber === event.target.value);
                    handleTrainChange(event, schedule);
                }}
            >
                {schedules.length === 0 && !isLoading && (
                    <div className="empty-message">
                        {errorMessage 
                            ? errorMessage
                            : hasSearched 
                                ? '' 
                                : '  '}
                    </div>
                )}
                {schedules.map((schedule, index) => (
                    <div key={index} className="schedule-card"
                        onClick={() => {
                            if (!isConfirm) {
                                //  TicketBox
                                onTrainSelect({
                                    fromStation,
                                    toStation,
                                    selectedDate,
                                    departureTime: schedule.departureTime,
                                    arrivalTime: schedule.arrivalTime,
                                    target: schedule.trainNumber,
                                    price: schedule.price,
                                    priceColor: schedule.priceColor,
                                    isConfirm: false,
                                    alternative: []
                                });
                                //  URL  ticket 
                                updateUrlToTicket(selectedDate, schedule.trainNumber, fromStation, toStation);
                                onNextStep();
                            } else {
                                // 
                                handleTrainChange({ target: { value: schedule.trainNumber } }, schedule);
                            }
                        }}
                    >
                        <div className="time-info">
                            <span className="schedule-time departure text-center">{schedule.departureTime}</span>
                            <span className="train-number-outer">
                                <div className="train-number">
                                    {schedule.trainNumber}
                                </div>
                                <div className="duration">
                                    <span className="icon"></span> {schedule.duration || util.time_between(schedule.departureTime, schedule.arrivalTime)}
                                </div>
                            </span>
                            <span className="schedule-time arrival text-center">{schedule.arrivalTime}</span>
                            <span className="flex-1 d-flex flex-column justify-content-space-between">
                                {isConfirm ? (
                                    // /
                                    <>
                                <FormControlLabel
                                    className="justify-content-center"
                                    value={schedule.trainNumber}
                                    control={<Radio sx={{
                                        color: '#ff6b00',
                                        '&.Mui-checked': {
                                            color: '#ff6b00',
                                        },
                                        padding: '4px'
                                    }}/>}
                                    label={isWideScreen ? "" : ""}
                                    sx={{
                                        margin: 0,
                                        alignItems: 'center',
                                        '.MuiFormControlLabel-label': {
                                            fontSize: '0.9rem',
                                            color: '#666'
                                        }
                                    }}
                                />
                                <FormControlLabel
                                    className="justify-content-center"
                                            disabled={
                                                selectedTrain === schedule.trainNumber || 
                                                (alternativeTrains.length >= MAX_ALTERNATIVE_TRAINS && !alternativeTrains.includes(schedule.trainNumber))
                                            }
                                    onClick={(e) => e.stopPropagation()}
                                    control={<Checkbox
                                        checked={selectedTrain !== schedule.trainNumber && alternativeTrains.includes(schedule.trainNumber)}
                                        onChange={(e) => handleAlternativeChange(e, schedule.trainNumber)}
                                        sx={{
                                            color: '#ff6b00',
                                            '&.Mui-checked': {
                                                color: '#ff6b00',
                                            },
                                            padding: '4px'
                                        }}/>}
                                    label={isWideScreen ? "" : ""}
                                    sx={{
                                        margin: 0,
                                        alignItems: 'center',
                                        '.MuiFormControlLabel-label': {
                                            fontSize: '0.9rem',
                                            color: '#666'
                                        }
                                    }}
                                />
                                    </>
                                ) : (
                                    // 
                                    <div className="discount-info" style={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        height: '100%',
                                        padding: '4px'
                                    }}>
                                        <span style={{
                                            color: '#fff',
                                            backgroundColor: schedule.priceColor || '#ff6b00',
                                            fontWeight: 'bold',
                                            fontSize: '0.85rem',
                                            padding: '0.5em',
                                            borderRadius: '4px',
                                            width: '5em',
                                            textAlign: 'center',
                                            display: 'inline-block'
                                        }}>
                                            {schedule.price || ''}
                                        </span>
                                    </div>
                                )}
                            </span>
                        </div>
                    </div>
                ))}
            </RadioGroup>

            <div className="navigation-buttons">
                <button 
                    className="nav-button prev" 
                    onClick={handlePrevTrains}
                    disabled={!prevTime || isLoading}
                >
                    <span className="arrow"></span> 
                </button>
                <button 
                    className="nav-button next"
                    onClick={handleNextTrains}
                    disabled={!nextTime || isLoading}
                >
                     <span className="arrow"></span>
                </button>
            </div>
            {selectedTrain && isConfirm && (
                <div className="selection-bar">
                    <div className="selected-trains">
                        <div className="train-group">
                            <span className="train-label"></span>
                            <Chip
                                label={selectedTrain}
                                onDelete={handleRemoveSelectedTrain}
                                deleteIcon={<CloseIcon sx={{ fontSize: '14px' }} />}
                                size="small"
                                sx={{
                                    bgcolor: '#ff6b00',
                                    color: 'white',
                                    fontWeight: 'bold',
                                    borderRadius: '4px',
                                    '& .MuiChip-deleteIcon': {
                                        color: 'white',
                                        '&:hover': {
                                            color: '#ffcca6'
                                        }
                                    }
                                }}
                            />
                        </div>
                        {alternativeTrains.filter(t => t !== selectedTrain).length > 0 && (
                            <div className="train-group">
                                <span className="train-label"></span>
                                {alternativeTrains.filter(t => t !== selectedTrain).map(train => (
                                    <Chip
                                        key={train}
                                        label={train}
                                        onDelete={() => handleRemoveAlternativeTrain(train)}
                                        deleteIcon={<CloseIcon sx={{ fontSize: '14px' }} />}
                                        size="small"
                                        sx={{
                                            bgcolor: '#fff',
                                            color: '#ff6b00',
                                            border: '1px solid #ff6b00',
                                            fontWeight: 'bold',
                                            borderRadius: '4px',
                                            '& .MuiChip-deleteIcon': {
                                                color: '#ff6b00',
                                                '&:hover': {
                                                    color: '#cc5500'
                                                }
                                            }
                                        }}
                                    />
                                ))}
                            </div>
                        )}
                    </div>
                    <Button 
                        className="fs-5" 
                        variant="contained" 
                        color="primary"
                        onClick={() => {
                            //  URL  ticket 
                            updateUrlToTicket(selectedDate, selectedTrain, fromStation, toStation, alternativeTrains);
                            onNextStep();
                        }}
                    >
                        
                    </Button>
                </div>
            )}
        </div>
    );
};

export default TimeTable; 