    const saveBookingToLocalStorage = (emailVal, passengersVal) => {
        try {
            const data = {
                email: emailVal,
                passengers: passengersVal
            };
            localStorage.setItem(BOOKING_STORAGE_KEY, JSON.stringify(data));
        } catch (e) {
            console.error('Error saving booking data to localStorage:', e);
        }
    };

    //  email  localStorage
